export default {
  data() {
    return {
      active_devis: false,
      form: {
        name: '',
        phone: '',
        email: '',
        prestation: '',
        lieu: '',
        date: '',
        convives: '',
        heure: '',
        details: '',
      },
      loading: false,
      state: true,
      goodNews: false,
      error: false,
      devis: true, 
    }
  },
  methods: {
    submitP(e) {
      e.preventDefault()
      console.log({ ...this.form })
      ;(this.loading = true), (this.state = false)
        this.$axios
          .post('https://apimyterroir.rouxnicolas.fr/send', { ...this.form })
          .then(
            (res) =>
            (this.form = ''),
            (this.loading = false),
            (this.state = true),
            (this.goodNews = true)
          )
          .catch((error) => {
             ;(this.error = true),
            (this.form = ''),
            (this.loading = false),
            (this.state = true)
          })
    }
  }
}
